.card-body {
    min-width: 120px;
    background-color: transparent;
    font-size: 1.1em;
    color:white;
    

}
.d-flex .card {
    background-color: transparent;
    border-style: none;  
}


@media screen and (min-width: 576px) {
    .d-flex .card { 
    border-radius: 10px;
    min-width: 10vw;
    min-height: 10vh;
    }
  } 

@media screen and (max-width: 575px) {
    .small-screen{
        padding: 10px;
        border-top-style: solid;
        border-top-color: rgba(255, 255, 255, .2);;
        justify-content:space-between;
        width: 100%;
    }

    .fixweathericon{
        margin-left: -20px;
    }
} 